<template>
  <top-nav> 购物车 </top-nav>
  <bottom-nav></bottom-nav>
  <div class="shopcart-main">
    <div class="shopcart-list">
      <van-checkbox-group
        v-model="result"
        class="goodscard"
        v-for="(item, index) in list"
        :key="index"
        @change="groupChange"
      >
        <van-checkbox :name="item.id"></van-checkbox>
        <!--商品卡牌-->
        <van-swipe-cell>
          <van-card
            :num="item.goods.stock"
            :price="item.goods.price + '.00'"
            :title="item.goods.title"
            :thumb="item.goods.cover_url"
          />

          <!--数量按钮-->
          <van-stepper
            integer
            :min="1"
            :max="item.goods.stock"
            :model-value="item.num"
            :name="item.id"
            async-change
            @change="onChange"
          ></van-stepper>
          <!--删除按钮-->
          <template #right>
            <van-button
              square
              text="删除"
              type="danger"
              class="delete-button"
            />
          </template>
        </van-swipe-cell>
      </van-checkbox-group>
    </div>

    <van-submit-bar
      class="submit-nav"
      :price="totalPrice * 100"
      button-text="提交订单"
      
    >
      <van-checkbox v-model="checkAll" @click='Allcheck'>全选</van-checkbox>
    </van-submit-bar>
    <!--没有商品时显示这个-->
    <div class="empty" v-if="!list.length" @click="goHome">
      <van-image
        width="200"
        height="150"
        src="https://cdn.jsdelivr.net/npm/@vant/assets/cat.jpeg"
        class="empty-cart"
        alt="空购物车"
      />
      <div class="title">购物车空空如也</div>
      <van-button round color="#034e94" type="primary" block width="95%"
        >前往购物</van-button
      >
    </div>
  </div>
</template>

<script>
import BottomNav from "../../components/BottomNav.vue";
import TopNav from "../../components/TopNav.vue";
import { ref, reactive, toRefs, onMounted, computed } from "vue";
import { getCart, checkedCart, modifyCart } from "../../network/cart";
import { Toast } from "vant";
import { useRouter } from "vue-router";
export default {
  name: "ShopCart",
  components: { TopNav, BottomNav },
  setup() {
    const router = useRouter();
    const checked = ref(["a"]);
    const state = reactive({
      list: [],
      result: [],
      checkAll: false,
    });

    //加载购物车列表
    const init = () => {
      Toast.loading({ message: "加载中", forbidClick: true });
      getCart("include=goods").then((res) => {
        console.log(res);
        state.list = res.data;
        state.result = res.data
          .filter((x) => x.is_checked == 1)
          .map((item) => item.id);
      });
    };

    //修改物品选中状态
    const groupChange = (result) => {
      checkedCart({ cart_ids: result });

      if (state.result.length == state.list.length) {
        state.checkAll = true;
      } else {
        state.checkAll = false;
      }
    };

    //修改物品数量
    const onChange = (value, detail) => {
      Toast.loading({ message: "修改中", forbidClick: true });
      modifyCart(detail.name, { num: value }).then((res) => {});
      state.list.forEach((item) => {
        if (item.id == detail.name) {
          item.num = value;
        }
      });
      
    };

    const goHome = () => {
      router.push({ path: "/home" });
    };

    //物品总价
    const totalPrice = computed(() => {
      let sum = 0;
      state.list
        .filter((item) => state.result.includes(item.id))
        .forEach((item) => {
          sum += parseInt(item.goods.price) * parseFloat(item.num);
        });
      return sum;
    });

    //全选
    const Allcheck = () => {
        state.checkAll = !state.checkAll
        if(!state.checkAll){
            state.result = state.list.map((item) => item.id)
            state.checkAll = false
        }else{
            state.result = []
            state.checkAll = true
        }
        console.log(state.checkAll)
    }
    onMounted(() => {
      init();
    });

    return {
      checked,
      ...toRefs(state),
      groupChange,
      goHome,
      totalPrice,
      onChange,
      Allcheck
    };
  },
};
</script>

<style lang="scss" scoped>
.shopcart-main {
  width: 100%;
  margin-top: 60px;
  .goodscard {
    width: 95%;
    background-color: #fff;
    margin-left: 2.5%;
    display: flex;
    border-radius: 10px;
    text-align: left;
    margin-bottom: 10px;
    padding-left: 5px;
  }
}
.van-card {
  background-color: #fff;
  font-size: 16px;
}
.van-swipe-cell {
  width: 100%;
  border-radius: 10px;
}
.delete-button {
  height: 100%;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.van-card__content {
  margin-left: 20px;
}
.van-checkbox {
  width: 25px;
}
button {
  width: 95%;
  margin-left: 2.5%;
}
.empty {
  margin-top: 250px;
}
.submit-nav {
  margin-bottom: 60px;
}
.van-stepper {
  float: right;
  margin: 10px;
}
</style>
